@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap'); */

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	outline: none;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #f1f1f2;
	font-family: 'Lobster', sans-serif;
	background-image: url(/todo-app/static/media/background.c8712d3d.jpg);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.empty-todo-warning {
	position: relative;
	padding-top: 250px;
	font-size: 25px;
	text-align: center;
}

.empty-todo-warning::before {
	content: '';
	position: absolute;
	left: 50%;
	bottom: -114px;
	transform: translateX(-50%) rotate(39deg);
	background-image: url(/todo-app/static/media/wing.bd7e9622.png);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	width: 75px;
	height: 79px;
}

.bttn {
  padding: 4px 11px;
  border-radius: 50%;
  border: 2px outset -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
  background-color: #f1f1f2;
  transition: 0.3s;
  cursor: pointer;
  font-family: 'Lobster', sans-serif;
  line-height: normal;
  line-height: initial;
}

.bttn:hover {
  color: white;
}

.bttn:focus {
	outline: none;
}

.wrapper {
	max-width: 800px;
  margin: 40px auto;
  padding: 0 15px;
}

@media (max-width: 414px) {
  .wrapper {
    margin-top: 15px;
  }
}

.todo__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
          column-gap: 15px;
  padding: 0 15px;
  border: 1px solid #f1f1f2;
  background-color: white;
  transition: 0.7s;
}

.todo__item > .todo__delete-bttn {
opacity: 0;
}

.todo__item:hover > .todo__delete-bttn {
  opacity: 1;
}

.todo__item:first-child {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.todo__item:last-child {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.todo__checkbox {
  width: 30px;
  height: 30px;
}

.todo__checkbox:checked + label {
  text-decoration: line-through;
}

.todo__text {
  margin-right: auto;
  margin-bottom: 0;
  line-height: 52px;
  font-size: 18px;
  flex: 1 1;
  cursor: pointer;
  padding-left: 20px;
  font-family: 'Lobster', sans-serif;
}

.todo__delete-bttn {
  background-color: rgba(255, 66, 14, 0.1);
  opacity: 0;
}

.todo__delete-bttn:hover {
  background-color: rgba(255, 66, 14, 1);
}

.finished {
  background-color: rgb(230, 230, 230);
}

ul {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	margin-bottom: 75px;
}
h1 {
	margin-bottom: 25px;
	text-align: center;
}

@media (max-width: 414px) {
  h1 {
		font-size: 21px;
		margin-bottom: 15px;
  }
}

.todo__input-form {
	display: flex;
  justify-content: space-between;
	align-items: center;
	grid-column-gap: 15px;
	-webkit-column-gap: 15px;
	        column-gap: 15px;
	padding: 12px 12px;
	border: 1px solid #f1f1f2;
	background-color: rgb(228, 224, 224);
	border-radius: 5px;
	position: fixed;
	bottom: 10px;
	left: 50%;
	transform: translateX(-50%);
	width: 770px;
}

.todo__input-field {
	padding: 7px 8px;
	line-height: 20px;
	font-size: 18px;
	font-family: 'Lobster', sans-serif;
	flex: 1 1;
	border: 1px solid #f1f1f2;
	border-radius: 5px;
}

.todo__input-field::-webkit-input-placeholder {
	color: rgb(160, 160, 160);
}

.todo__input-field:-ms-input-placeholder {
	color: rgb(160, 160, 160);
}

.todo__input-field::placeholder {
	color: rgb(160, 160, 160);
}

.todo__add-bttn {
	background-color: rgba(84, 243, 132, .2);
}

.todo__add-bttn:hover {
	background-color: rgba(84, 243, 132, 1);
}

@media (max-width: 800px) {
	.todo__input-form {
		bottom: 0px;
		left: 15px;
		right: 15px;
		transform: none;
		width:auto;
		width:initial;
	}
}
